<template>
  <el-container class="ep-site__container" style="height: 100%;">
    <el-aside width="196px" class="ep-aside">
      <el-header height="56px" class="ep-aside__header">
        <div class="header-title">
          <span>人口管控系统</span>
        </div>
      </el-header>
      <el-menu
        router
        unique-opened
        :default-active="$route.path"
        class="ep-menu"
        @open="handleOpen"
        @close="handleClose"
        background-color="#001529"
        text-color="hsla(0,0%,100%,.65)"
        active-text-color="#fff">
        <el-menu-item
          v-for="item in menuList"
          :key="item.name"
          :index="item.path"
        >
          <span slot="title">{{item.name}}</span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header height="56px" class="ep-container__header">
        <div class="user-info">
          <el-dropdown trigger="click">
            <span class="el-dropdown-link">
              欢迎您，{{officerInfo.officerName}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <div @click="signOutFun">退出登录</div>
              </el-dropdown-item>
              <el-dropdown-item>
                <div @click="updateFun">修改密码</div>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-main class="ep-container__main">
        <el-breadcrumb class="content-breadcrumb" separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item
            v-for="(item, index) in routes"
            :key="index"
            :to="{ path: item.path }"
          >
            {{item.name}}
          </el-breadcrumb-item>
        </el-breadcrumb>
        <div class="content-wrapper">
          <router-view/>
        </div>
        <!--<el-footer class="content-footer" height="42px">-->
        <!--<span>Copyright © 2018 大数据与智能供应链事业部-运营数据研发部 版权所有 </span>-->
        <!--</el-footer>-->
      </el-main>
    </el-container>
    <el-dialog
      title="修改密码"
      :append-to-body="true"
      :visible.sync="dialogVisible"
      width="30%"
      :close="closeDialog">
      <el-form ref="pwdForm" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="旧密码" prop="oldPwd">
          <el-input type="password" size="small" placeholder="请输入" clearable v-model="form.oldPwd"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="newPwd">
          <el-input type="password" size="small" placeholder="请输入" clearable v-model="form.newPwd"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button size="small" @click="closeDialog">取 消</el-button>
        <el-button size="small" type="primary" @click="savePwd">确 定</el-button>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import Cookies from 'js-cookie'
import Service from './service'

export default {
  name: 'DefaultLayout',
  data() {
    return {
      routes: [],
      officerInfo: {},
      menuList: [],
      dialogVisible: false,
      form: {
        newPwd: '',
        oldPwd: ''
      },
      rules: {
        oldPwd: [
          { required: true, message: '请输入旧密码', trigger: 'blur' }
        ],
        newPwd: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, message: '密码最少6个字符', trigger: 'blur' }
        ],
      }
    }
  },
  watch: {
    '$route': 'buildRoutes'
  },
  created() {
    this.getMenuList()
    this.buildRoutes(this.$route, null)
  },
  mounted() {
    console.log(this.$route.params)
  },
  methods: {
    getMenuList() {
      if (Cookies.get('officerInfo')) {
        this.officerInfo = JSON.parse(Cookies.get('officerInfo'))
      }
      if (Cookies.get('menuList')) {
        const menuList = JSON.parse(Cookies.get('menuList'))
        this.menuList = menuList.filter(v => !v.isChildren)
      }
    },
    signOutFun() {
      Cookies.remove('isLogin')
      this.$router.push({ path: '/login' })
    },
    async updatePwd() {
      // TODO officeNo需要修改成真实的
      let params = {
        ...this.form,
        officerNo: this.officerInfo.officerNo
      }
      const res = await Service.updatePassword(params)
      if (res) {
        this.$message.success('密码修改成功！')
        this.closeDialog()
      }
    },
    savePwd() {
      this.$refs.pwdForm.validate((valid) => {
        if (valid) {
          this.updatePwd()
        } else {
          return false;
        }
      });
    },
    closeDialog() {
      this.$refs.pwdForm.resetFields()
      this.dialogVisible = false
    },
    updateFun() {
      this.dialogVisible = true
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    buildRoutes(newVal, oldVal) {
      // 刷新时查找cookie是否存在
      if (Cookies.get('routes') && !oldVal) {
        this.routes = JSON.parse(Cookies.get('routes'))
        return
      }
      let routes
      const prevRoute = oldVal && oldVal.matched[1]
      const currentRoute = newVal.matched[1]
      if (oldVal && currentRoute.meta.isChildren) {
        routes = Array.of(prevRoute, currentRoute)
      } else {
        routes = Array.isArray(currentRoute) ? currentRoute : Array.of(currentRoute)
      }
      this.routes = routes.map(v => {
        return {
          name: v.meta.name,
          path: v.path
        }
      })
      Cookies.set('routes', JSON.stringify(this.routes), { expires: 365 })
    }
  }
}
</script>

